import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Button } from "primereact/button";
import { useEffect, useRef, useState } from "react";
import FetchHelper from '../../helpers/FetchHelper';
import { Dialog } from 'primereact/dialog';
import { Toast } from "primereact/toast";
import { InputText } from 'primereact/inputtext';
import { Loading } from '../../layouts/Loading';
export default function Domaine() {

    const [state,setState]=useState({
        domaine:"",
    });

    const toast=useRef();
    
    const [domaine,setDomaine]=useState([]); 
    
    const [visible,setVisible]=useState(false);

    useEffect(()=>{
        getAllDomaine();
    },[]);

    const handleChange=(event)=>{
        const { name, value } = event.target;
        setState(prevState => ({ ...prevState, [name]: value }));
    }

    const handleSubmit=(event)=>{
        event.preventDefault();
        let donne=new URLSearchParams();
        donne.append("domaine",state.domaine);
        FetchHelper.getDataPost("domaine/add",donne).then(response=>{
            console.log(response);
        }).catch(eror=>{
            console.log(eror);
        });
        window.location.reload();
    }

    const deleteDomaine=(id)=>{
        let donne=new URLSearchParams();
        donne.append("id",id);
        console.log("delete :"+id);
        FetchHelper.getDataPost("domaine/delete",donne).then(response=>{
            console.log(response);
            if(response.error!=null){
                toast.current.show({severity:'error', summary: 'Error', detail:"Impossible a supprimer", life: 3000});
            }
            else{
                window.location.reload();
            }
        }).catch(eror=>{
            toast.current.show({severity:'error', summary: 'Error', detail:"Impossible a supprimer", life: 3000});
        });
    }

    const [loading,setLoading]=useState(false);


    const getAllDomaine=()=>{
        setLoading(true);
        const fetch=FetchHelper.getData("domaine/getAll");
        fetch.then(response=>{
            setDomaine(response);
        })
        .catch(error=>console.log(error))
        .finally(()=>{
            setLoading(false);
        });
    }

    const header = (
        <div className="
        text-base text-gray-500
        flex
        justify-between  items-center">
            <span className="text-2xl text-900 font-bold">Liste des domaine :</span>
            <Button icon="pi pi-plus" rounded raised onClick={() => setVisible(true)}/>
        </div>
    );
    const footer = `Il y a ${domaine ? domaine.length : 0} articles.`;

    const supprimerBody = (domaine) => {
        return <Button icon="pi pi-trash" rounded severity="danger" aria-label="supprimer" onClick={()=>{deleteDomaine(domaine.id)}} />;
    };

    return(
        <>
            {
                loading ?(
                    <Loading/>
                ):(
                    <div>
                        <DataTable value={domaine} header={header} footer={footer} paginator stripedRows rows={5}  sortMode="multiple" emptyMessage="No article found." rowsPerPageOptions={[5, 10, 25, 50]} removableSort scrollable tableClassName="w-full h-full" tableStyle={{ minWidth: '60rem' }}>
                            <Column field="id" sortable header="ID"></Column>
                            <Column field="type" header="Nom"></Column>
                            <Column body={supprimerBody}/>
                        </DataTable>
                        <Dialog position="top-left" header="Ajouter une nouvel article" maximizable visible={visible} style={{ width: '50vw' }} onHide={() => setVisible(false)}>
                        <form onSubmit={handleSubmit}>    
                                        <span className="flex flex-col m-6 p-float-label">
                                            <InputText required className="w-full" id="nom" onChange={handleChange} value={state.domaine} name="domaine"/>
                                            <label htmlFor="nom">Nom</label>
                                        </span>
                                        <span className="flex flex-col m-6">
                                            <Button type="submit" label="Add" icon="pi pi-plus" />
                                        </span>
                            </form>
                        </Dialog>
                        <Toast ref={toast}/>
                    </div>
                )
            }
        </>
    );  
}