export default class FetchHelper{

    static urlgen(url){
        return "https://infoia-production-1372.up.railway.app/"+url;
    } 

    static getData=async (url)=>{
        let token=localStorage.getItem("token");
        console.log(FetchHelper.urlgen(url));
        const response = await fetch(FetchHelper.urlgen(url),
            {
                crossDomain: true,
                method: 'GET',
                headers: { "authorization": token }
            }
        );
        const data =await response.json();
        return data;    
    }


    static getDataPost = async (url, info) => {
        let token=localStorage.getItem("token");
        const response = await fetch(FetchHelper.urlgen(url), {
            method: "POST",
            body: info,
            headers: {
                "authorization":token
            }
        });
        const data = await response.json();
        return data;
    }

}