import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Button } from "primereact/button";
import { useEffect, useState } from "react";
import FetchHelper from '../../helpers/FetchHelper';

import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from 'primereact/inputtextarea';
import { Dropdown } from 'primereact/dropdown';
import { Loading } from '../../layouts/Loading';
export default function Article() {

    const [state,setState]=useState({
        title:"",
        description:"",
        content:"",
        datePub:""
    });

    const [loading,setLoading]=useState(false);

    const [domaine,setDomaine]=useState([]); 

    const [selectDomaine,setSelectDomaine]=useState(null);

    const [visible,setVisible]=useState(false);

    const [selectedFile,setSelectedFile]=useState(null);

    const [article,setArticle]=useState([{
        id:0,
        title:"title",
        description:"description ",
        content:"",
        datePub:"mai 0 0 ",
        type:"Domaine 0",
        photo:""
    }]);

    const handleSubmit=(event)=>{
        event.preventDefault();
        let donne=new FormData();
        donne.append("title",state.title);
        donne.append("description",state.description);
        donne.append("content",state.content);
        donne.append("datePub",state.datePub);
        donne.append("domaine",selectDomaine);
        donne.append("photo",selectedFile);
        console.log(state);
        console.log(selectedFile);
        console.log(selectDomaine);
        fetch('http://localhost:8080/article/add', {
            method: 'POST',
            body: donne
          })
          .then(response => response.json())
          .then(res=>{
            console.log(res);
          })
          .catch(error => {
            // Gérer l'erreur
            console.log(error);
          });
          
        window.location.reload();
    }

    const handleChangeFile=(event)=>{
        setSelectedFile(event.target.files[0]);
    }

    const handleChange = (event) => {
        const { name, value } = event.target;
        setState(prevState => ({ ...prevState, [name]: value }));
    };

    const getArticle=()=>{
        setLoading(true);
        let url="article/getAll";
        let fetch=FetchHelper.getData(url);
        fetch.then(response=>{
            console.log(response);
            setArticle(response.data);
        })
        .catch(error=>console.log(error))
        .finally(()=>{
            setLoading(false);
        });
    }

    const getAllDomaine=()=>{
        const fetch=FetchHelper.getData("domaine/getAll");
        fetch.then(response=>{
            setDomaine(response);
        })
        .catch(error=>console.log(error));
    }

    useEffect(()=>{
        getArticle();
        getAllDomaine();
    },[]);

    const imageBodyTemplate = (article) => {
        return <img src={`data:image/png;base64,${article.photo}`} alt={article.title} className="w-48 shadow-2 border-round" />;
    };

    const header = (
        <div className="
        text-base text-gray-500
        flex
        justify-between  items-center">
            <span className="text-2xl text-900 font-bold">Liste des articles :</span>
            <Button icon="pi pi-plus" rounded raised onClick={() => setVisible(true)}/>
        </div>
    );
    const footer = `Il y a ${article ? article.length : 0} articles.`;

    const deleteArticle=(id)=>{
        let donne=new URLSearchParams();
        donne.append("id",id);
        console.log("delete :"+id);
        FetchHelper.getDataPost("article/delete",donne).then(response=>{
            console.log(response);
            if(response.error==null){
                window.location.reload();
            }
        }).catch(eror=>{
            console.log(eror);;
        });
    }

    const supprimerBody = (article) => {
        return <Button icon="pi pi-trash" rounded severity="danger" aria-label="supprimer" onClick={()=>{deleteArticle(article.id)}} />;
    };

    return(
        <>
            {
                loading ?(
                    <Loading/>
                ):(
                    <div className='card'>
                        <DataTable value={article} header={header} footer={footer} paginator stripedRows rows={5}  sortMode="multiple" emptyMessage="No article found." rowsPerPageOptions={[5, 10, 25, 50]} removableSort scrollable tableClassName="w-full h-full" tableStyle={{ minWidth: '60rem' }}>
                                    <Column field="id" sortable header="ID"></Column>
                                    <Column field="title" header="Titre"></Column>
                                    <Column header="Image" body={imageBodyTemplate}></Column>
                                    <Column field="description" header="Description"></Column>
                                    <Column field="content" header="Content"></Column>
                                    <Column field="datePub" header="Date de publication"></Column>
                                    <Column field="type" header="Domaine"></Column> 
                                    <Column body={supprimerBody} /> 
                        </DataTable>

                        <Dialog position="top-left" header="Ajouter une nouvel article" maximizable visible={visible} style={{ width: '50vw' }} onHide={() => setVisible(false)}>    
                            <form onSubmit={handleSubmit}>    
                                        <span className="flex flex-col m-6 p-float-label">
                                            <InputText required className="w-full" id="nom" onChange={handleChange} value={state.title} name="title"/>
                                            <label htmlFor="nom">Titre</label>
                                        </span>

                                        <span className="flex flex-col m-6 p-float-label">
                                            <InputText required className="w-full" id="nom" onChange={handleChange} value={state.description} name="description"/>
                                            <label htmlFor="nom">Description</label>
                                        </span>

                                        <span className="flex flex-col m-6 ">
                                            <InputText type="date" required className="w-full" id="nom" onChange={handleChange} value={state.datePub} name="datePub"/>
                                            <label htmlFor="nom">Date de publication</label>
                                        </span>

                                        <span className="flex flex-col m-6 p-float-label">
                                            <InputTextarea required className="w-full" id="nom" onChange={handleChange} value={state.content} name="content"/>
                                            <label htmlFor="nom">Content</label>
                                        </span>

                                        <span className="flex flex-col m-6">
                                            <input type="file" accept="image/*" required className="w-full" id="nom" onChange={handleChangeFile} name="selectedFile"/>
                                        </span>

                                        <span className="flex flex-col m-6">
                                            <Dropdown value={selectDomaine} onChange={(e) => setSelectDomaine(e.value)} options={domaine} optionLabel="type" optionValue="id" placeholder="Selecte un domaine" className="w-full md:w-14rem"/>
                                        </span>





                                        <span className="flex flex-col m-6">
                                            <Button type="submit" label="Add" icon="pi pi-plus" />
                                        </span>
                            </form>
                        </Dialog>
                    </div>
                )
            }
        </>
    );
}