import { useEffect, useRef, useState } from "react";
import FetchHelper from "../../helpers/FetchHelper";
import { useNavigate } from "react-router-dom";
import {InputText} from "primereact/inputtext";
import { Password } from 'primereact/password';
import { Button } from 'primereact/button';
import { Toast } from "primereact/toast";
import { Loading } from "../../layouts/Loading";

export default function Login() {

    
    const navigate=useNavigate();
    const toast=useRef();
    const [loading,setLoading]=useState(false);
    useEffect(()=>{
        const verificationSession=()=>{
            const token=localStorage.getItem("token");
            if(token!=null){
                const fetch=FetchHelper.getData("back/verifiSession");
                fetch.then(response=>{
                    if(response.code==null){
                        navigate("/acceuil");
                    }
                })
                .catch(toast.current.show({severity:'error', summary: 'Error', detail:"Session expiré", life: 3000}))
            }
        }
        verificationSession();
        document.title="Login";
    },[navigate])

    const [state,setState]=useState({
        nom:"admin",
        password:"admin"
    });

    const sendData=()=>{
        setLoading(true);
        var donnee=new URLSearchParams();
        donnee.append("nom",state.nom);
        donnee.append("password",state.password);
        const fetch=FetchHelper.getDataPost("back/login",donnee);
        fetch.then(response=>{
            if(response.code!=null){
                toast.current.show({severity:'error', summary: 'Error', detail:response.message, life: 3000});
            }
            else{
                localStorage.setItem("token",response.token);
                navigate("/acceuil");
            }
        })
        .catch(error=>console.log(error))
        .finally(()=>{
            setLoading(false);
        })
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        sendData();
    };

    const handleChange = (event) => {
        const { name, value } = event.target;
        setState(prevState => ({ ...prevState, [name]: value }));
    };

    return(
        <>
            {
                loading ?(
                    <Loading/>
                ):(
                    <div className="w-full min-h-screen  flex items-center justify-center">
                        <div className="w-full h-screen flex items-center justify-center">
                            <div className="w-full sm:w-5/6 md:w-2/3 lg:w-1/2 xl:w-1/3 2xl:w-1/4 h-full flex items-center justify-center">
                                <div className="w-full px-12">
                                    <h2 className="text-center text-2xl font-bold tracking-wide text-gray-300">Login</h2>

                                    <form onSubmit={handleSubmit} className="my-8 text-sm">
                                        <span className="flex flex-col m-6 p-float-label">
                                            <InputText required className="w-full" id="nom" keyfilter={"alpha"} onChange={handleChange} value={state.nom} name="nom"/>
                                            <label htmlFor="nom">Nom</label>
                                        </span>

                                        <span className="p-float-label flex flex-col m-6">
                                            <Password id="password" inputClassName="w-full" className="w-full" name="password" onChange={handleChange} value={state.password}  toggleMask/>
                                            <label htmlFor="password">Password</label>
                                        </span>

                                        <span className="p-float-label flex flex-col m-6">
                                            <Button label="Sign In" className="loginBtn w-full h-10 font-medium text-sm"/>
                                        </span>

                                    </form>

                                </div>
                            </div>

                            <div className="imageBack hidden lg:flex lg:w-1/2 xl:w-2/3 2xl:w-3/4 h-full bg-cover"></div>

                        </div>
                    </div>
                )
            }
            <Toast ref={toast}/>
        </>
    );
}