import { useNavigate } from "react-router-dom";
import image from "./../assets/images/bg.jpg";
export default function CardAdmin() {

    const navigate=useNavigate();

    const changePage=(page)=>{
        if(page===1){
            navigate("/acceuil/crud-article");
        }
        else{
            navigate("/acceuil/crud-domaine");
        }
    }

    return(
        <div className="grid grid-flow-col justify-center gap-4">
            <div className="wrapper m-10 antialiased text-gray-200">
                <div>
    
                    <img src={image} alt=" random imgee" className="w-full object-cover object-center rounded-lg shadow-md"/>    
    
                    <div className="relative px-4 -mt-16  ">
                        <div onClick={()=>changePage(1)} className="bg-gray-900 p-6 rounded-lg cursor-pointer shadow-lg">
                            <div className="flex items-baseline">
                                <span className="bg-teal-200 text-teal-800 text-xs px-2 inline-block rounded-full  uppercase font-semibold tracking-wide">
                                    CRUD
                                </span>
                            </div>
                
                            <h4 className="mt-1 text-xl font-semibold uppercase leading-tight truncate">Article</h4>
            
                            <div className="mt-4">
                                <span className="text-teal-600 text-md font-semibold">Voir </span>
                            </div>  
                        </div>
                    </div>
            
                </div>
            </div>



            <div className="wrapper m-10 antialiased text-gray-200">
                <div>
    
                    <img src={image} alt=" random imgee" className="w-full object-cover object-center rounded-lg shadow-md"/>    
    
                    <div className="relative px-4 -mt-16  ">
                        <div onClick={()=>changePage(2)} className="bg-gray-900 p-6 rounded-lg cursor-pointer shadow-lg">
                            <div className="flex items-baseline">
                                <span className="bg-teal-200 text-teal-800 text-xs px-2 inline-block rounded-full  uppercase font-semibold tracking-wide">
                                    CRUD
                                </span>
                            </div>
                
                            <h4 className="mt-1 text-xl font-semibold uppercase leading-tight truncate">Domaine</h4>
            
                            <div className="mt-4">
                                <span className="text-teal-600 text-md font-semibold">Voir </span>
                            </div>  
                        </div>
                    </div>
            
                </div>
            </div>
        </div>
    );
}